// WeddingDetails.tsx

import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
  text-align: center;
  padding: 1rem;
`;

const Title = styled.h2`
  font-size: 1.3rem;
`;

const Info = styled.p`
  font-size: 1rem;
  margin-top: 0.5rem;
`;

const WeddingDetails = () => (
    <Container>
        <Title> </Title>
        <Info>2025 10 25 ()  1</Info>
        <Info>   3</Info>
    </Container>
);

export default WeddingDetails;
