import React from 'react';
import styled from 'styled-components';

const HeaderContainer = styled.div`
  text-align: center;
  padding: 2rem 1rem;
  background-color: #fef8f3;
`;

const Title = styled.h1`
  font-size: 2rem;
  font-weight: bold;
`;

const Subtitle = styled.p`
  margin-top: 0.5rem;
  font-size: 1.1rem;
  color: #555;
`;

const Header = () => (
    <HeaderContainer>
        <Title>  </Title>
        <Subtitle>2025 10 26   1</Subtitle>
    </HeaderContainer>
);

export default Header;
